getFormattedData = (orgData, userData) => {
let data  = JSON.parse(JSON.stringify(orgData));
  if(data && data.length > 0){
    data.forEach(element => {
      if(element.user_id){
        let userDetails = userData.filter(user => element.user_id === user.user_id);

        element.vendorName = userDetails && userDetails[0] && userDetails[0].fullname ? userDetails[0].fullname : ' '
        element.vendorCompany = userDetails && userDetails[0] && userDetails[0].companyname ? userDetails[0].companyname : ' '
      }
    });
  }

  return data

}

getCurrentPageData = (enqData, start, end) => {

  return enqData.slice(start, end )

}


componentDidMount(){
  if(this.props.data && this.props.userData && this.state.enqData === undefined){
    var enqData = this.getFormattedData(this.props.data, this.props.userData)
    if(enqData && enqData.length > 0){
      let currentPageData = this.getCurrentPageData(enqData, 0 , this.state.itemsCountPerPage)
      this.setState({
        enqData ,
        currentPageData,
        totalItemsCount : enqData.length
      })
    }
  }
}

