getState= (userData) => {
  if(userData.state_id !== null){
    let stateInfo = _.filter(this.props.stateData, function(state) { return state.state_id === userData.state_id });
    userData.state = stateInfo[0].name
  }else{
    userData.state = 'NA'
  }
  return userData
}



getUserInfo = (user_id) => {
  let userData =  _.filter(this.props.userData, function(user) { return user.user_id === user_id });
  if(userData && userData.length > 0){
    userData = this.getState(userData[0])
  }
  return userData
}


componentWillReceiveProps=(nextProps) => {


  if(this.props.data !== nextProps.data){
    var enqData = this.getFormattedData(nextProps.data, this.props.userData)
    if(enqData && enqData.length > 0){



      let {activePage, itemsCountPerPage} = this.state
      let start = (activePage -1 ) * itemsCountPerPage,
      end = start + itemsCountPerPage,
      currentPageData = this.getCurrentPageData(enqData, start, end)
    
    
     


      this.setState({
        enqData ,
        currentPageData,
        totalItemsCount : enqData.length
      })
    }
  }




}

