import React from 'react'
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'
import {appendActivatedRoutes, getOrderData} from '../actions/treeview'
//import enqList from '../components/enqList'
import EnqList from '../components/enqList'
import _ from 'lodash'
import {getUsers} from '../actions/login'
import {getStateData, } from '../actions/treeview'



class enquiryContainer extends React.Component {
  constructor(props) {
    super(props)
    this.userDetails =JSON.parse(localStorage.getItem('userInfo'))
  }

  
  componentDidMount() {

  var userInfo = JSON.parse(localStorage.getItem('userInfo'))
  if(userInfo && userInfo.user_id){

    this.props.getOrderData(userInfo.user_id)
  }
  if(this.props.userData === undefined){
    this.props.getUsers()
  }
  if(this.props.stateData === undefined){
    this.props.getStateData()
  }
    //this.props.getSubCategoryData()
  }

  componentWillReceiveProps=(nextProps) => {
    if(nextProps.isDataSubmitted && this.props.isDataSubmitted !== nextProps.isDataSubmitted){
      var userInfo = JSON.parse(localStorage.getItem('userInfo'))
      if(userInfo && userInfo.user_id){
    
        this.props.getOrderData(userInfo.user_id)
      }
    }
    if(this.props.isOrderUpdated !== nextProps.isOrderUpdated && nextProps.isOrderUpdated === true){
      var userInfo = JSON.parse(localStorage.getItem('userInfo'))
      if(userInfo && userInfo.user_id){
    
        this.props.getOrderData(userInfo.user_id)
      }

    }
  }

  setOrderItem() {
      var data =  this.props.orderData
      if(data && data.lenght > 0){
       data[0].orderItemObjs = [
         {
           hello : 'indaia',
           hello : 'indaia',
           hello : 'indaia',
           hello : 'indaia'
         },
         {
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia'
        },
        {
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia'
        },
        {
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia',
          hello : 'indaia'
        }
       ]
      }
      // var formattedArray = []
      // if(data && data.length > 0){
      //   formattedArray =_.chunk(data, 4)
      // }
    return (
      <div>
        {data && this.props.userData &&  this.props.stateData &&
        <EnqList 
          data = {data}
          history = {this.props.history}
          userData = {this.props.userData}
          stateData = {this.props.stateData}

        />}
      </div>
    )
  }
  render() {
    return (this.setOrderItem())
  }
}


function mapStateToProps(state) {
  return {
    activatedRoutes : state.treeview.activatedRoutes,
    //subCategoryData: state.dataItem.subCategorydata,
    //productData: state.dataItem.productdata,
    //categoryData: state.dataItem.categoryData,
    //isItemdataLoaded: state.dataItem.isItemdataLoaded,
    orderData :  state.treeview.orderData,
    userData : state.login.userData,
    isDataSubmitted : state.dataItem.isDataSubmitted,
    stateData: state.treeview.stateData,
    isOrderUpdated :state.dataItem.isOrderUpdated
    

    

  }
}

function mapDispatchtoProps(dispatch) {
  return bindActionCreators(
    {
      appendActivatedRoutes:appendActivatedRoutes,
      getOrderData:getOrderData,
      getUsers,getUsers,
      getStateData:getStateData,


    },
    dispatch
  )
}

export default connect(mapStateToProps, mapDispatchtoProps)(enquiryContainer)

